import { useState, useEffect } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { useAuth } from '../../contexts/AuthContext';
import { supabase, type Product } from '../../lib/supabase';
import Navbar from '../../components/feature/Navbar';
import ImageUploader from '../../components/base/ImageUploader';

const categories = [
  'Electronics',
  'Books & Stationery',
  'Fashion & Accessories',
  'Food & Beverages',
  'Sports & Fitness',
  'Home & Living',
  'Services',
  'Other',
];

export default function EditProduct() {
  const navigate = useNavigate();
  const { id } = useParams();
  const { user, profile } = useAuth();
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [formData, setFormData] = useState({
    name: '',
    description: '',
    category: '',
    price: '',
    priceType: 'fixed' as 'fixed' | 'contact',
    images: [] as string[],
    whatsapp_number: '',
  });

  useEffect(() => {
    if (!user || (profile?.role !== 'seller' && profile?.role !== 'admin')) {
      navigate('/marketplace');
      return;
    }

    if (id) {
      fetchProduct();
    }
  }, [id, user, profile, navigate]);

  const fetchProduct = async () => {
    if (!id) return;

    setLoading(true);
    try {
      const { data, error } = await supabase
        .from('products')
        .select('*')
        .eq('id', id)
        .single();

      if (error) throw error;

      if (data) {
        // Check if user owns this product or is admin
        if (data.seller_id !== user?.id && profile?.role !== 'admin') {
          navigate('/seller/dashboard');
          return;
        }

        setFormData({
          name: data.name || '',
          description: data.description || '',
          category: data.category || '',
          price: data.price?.toString() || '',
          priceType: data.price_type || 'fixed',
          images: data.images || [],
          whatsapp_number: data.whatsapp_number || '',
        });
      }
    } catch (err: any) {
      console.error('Error fetching product:', err);
      setError('Failed to load product');
    } finally {
      setLoading(false);
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setSaving(true);
    setError(null);

    try {
      const productData = {
        name: formData.name,
        description: formData.description,
        category: formData.category,
        price: formData.priceType === 'fixed' ? parseFloat(formData.price) : null,
        price_type: formData.priceType,
        images: formData.images.length > 0 ? formData.images : null,
        whatsapp_number: formData.whatsapp_number || null,
        updated_at: new Date().toISOString(),
      };

      const { error } = await supabase
        .from('products')
        .update(productData)
        .eq('id', id);

      if (error) throw error;

      navigate('/seller/dashboard');
    } catch (err: any) {
      setError(err.message || 'Failed to update product');
    } finally {
      setSaving(false);
    }
  };

  const handleImageUploaded = (url: string) => {
    setFormData(prev => ({
      ...prev,
      images: [...prev.images, url],
    }));
  };

  const removeImage = (index: number) => {
    setFormData(prev => ({
      ...prev,
      images: prev.images.filter((_, i) => i !== index),
    }));
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-teal-50 via-white to-blue-50 flex items-center justify-center">
        <div className="text-center">
          <i className="ri-loader-4-line text-4xl text-teal-600 animate-spin"></i>
          <p className="mt-4 text-gray-600">Loading product...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-teal-50 via-white to-blue-50">
      <Navbar />
      
      <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-6 sm:py-8 lg:py-12">
        <div className="mb-6 sm:mb-8">
          <h1 className="text-2xl sm:text-3xl lg:text-4xl font-bold text-gray-900">Edit Product</h1>
          <p className="mt-2 text-sm sm:text-base text-gray-600">Update your product information</p>
        </div>

        <div className="bg-white rounded-2xl shadow-lg p-6 sm:p-8">
          {error && (
            <div className="mb-6 p-4 bg-red-50 border border-red-200 rounded-lg flex items-start">
              <i className="ri-error-warning-line text-red-600 text-xl mr-3 mt-0.5"></i>
              <p className="text-sm text-red-600">{error}</p>
            </div>
          )}

          <form onSubmit={handleSubmit} className="space-y-6">
            <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
              <div className="sm:col-span-2">
                <label className="block text-sm font-semibold text-gray-700 mb-2">
                  Product Name <span className="text-red-500">*</span>
                </label>
                <input
                  type="text"
                  value={formData.name}
                  onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                  className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-teal-500 focus:border-transparent text-sm transition-all"
                  placeholder="Enter product name"
                  required
                />
              </div>

              <div className="sm:col-span-2">
                <label className="block text-sm font-semibold text-gray-700 mb-2">
                  Category <span className="text-red-500">*</span>
                </label>
                <select
                  value={formData.category}
                  onChange={(e) => setFormData({ ...formData, category: e.target.value })}
                  className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-teal-500 focus:border-transparent text-sm transition-all"
                  required
                >
                  <option value="">Select a category</option>
                  {categories.map((cat) => (
                    <option key={cat} value={cat}>
                      {cat}
                    </option>
                  ))}
                </select>
              </div>
            </div>

            <div>
              <label className="block text-sm font-semibold text-gray-700 mb-2">
                Description
              </label>
              <textarea
                value={formData.description}
                onChange={(e) => setFormData({ ...formData, description: e.target.value })}
                rows={4}
                className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-teal-500 focus:border-transparent text-sm transition-all resize-none"
                placeholder="Describe your product..."
              />
            </div>

            <div>
              <label className="block text-sm font-semibold text-gray-700 mb-3">
                Price Type
              </label>
              <div className="flex flex-col sm:flex-row gap-3">
                <label className="flex items-center cursor-pointer bg-gray-50 px-4 py-3 rounded-xl border-2 border-gray-200 hover:border-teal-500 transition-all flex-1">
                  <input
                    type="radio"
                    value="fixed"
                    checked={formData.priceType === 'fixed'}
                    onChange={(e) => setFormData({ ...formData, priceType: e.target.value as 'fixed' | 'contact' })}
                    className="mr-3 w-4 h-4 text-teal-600"
                  />
                  <div>
                    <span className="text-sm font-medium text-gray-900">Fixed Price</span>
                    <p className="text-xs text-gray-500">Set a specific price</p>
                  </div>
                </label>
                <label className="flex items-center cursor-pointer bg-gray-50 px-4 py-3 rounded-xl border-2 border-gray-200 hover:border-teal-500 transition-all flex-1">
                  <input
                    type="radio"
                    value="contact"
                    checked={formData.priceType === 'contact'}
                    onChange={(e) => setFormData({ ...formData, priceType: e.target.value as 'fixed' | 'contact' })}
                    className="mr-3 w-4 h-4 text-teal-600"
                  />
                  <div>
                    <span className="text-sm font-medium text-gray-900">Contact for Price</span>
                    <p className="text-xs text-gray-500">Negotiate with buyers</p>
                  </div>
                </label>
              </div>
            </div>

            {formData.priceType === 'fixed' && (
              <div>
                <label className="block text-sm font-semibold text-gray-700 mb-2">
                  Price (GHS)
                </label>
                <div className="relative">
                  <span className="absolute left-4 top-1/2 -translate-y-1/2 text-gray-500 font-medium">â‚µ</span>
                  <input
                    type="number"
                    step="0.01"
                    value={formData.price}
                    onChange={(e) => setFormData({ ...formData, price: e.target.value })}
                    className="w-full pl-10 pr-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-teal-500 focus:border-transparent text-sm transition-all"
                    placeholder="0.00"
                  />
                </div>
              </div>
            )}

            <div>
              <label className="block text-sm font-semibold text-gray-700 mb-2">
                WhatsApp Number (Optional)
              </label>
              <input
                type="tel"
                value={formData.whatsapp_number}
                onChange={(e) => setFormData({ ...formData, whatsapp_number: e.target.value })}
                className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-teal-500 focus:border-transparent text-sm transition-all"
                placeholder="+233 XX XXX XXXX"
              />
              <p className="mt-1 text-xs text-gray-500">Buyers can contact you directly on WhatsApp</p>
            </div>

            <div>
              <label className="block text-sm font-semibold text-gray-700 mb-4">
                Product Images
              </label>
              
              <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 gap-4 mb-4">
                {formData.images.map((url, index) => (
                  <div key={index} className="relative group">
                    <div className="aspect-square rounded-xl overflow-hidden bg-gray-100 border-2 border-gray-200">
                      <img
                        src={url}
                        alt={`Product ${index + 1}`}
                        className="w-full h-full object-cover"
                        loading="lazy"
                      />
                    </div>
                    <button
                      type="button"
                      onClick={() => removeImage(index)}
                      className="absolute -top-2 -right-2 w-8 h-8 bg-red-500 text-white rounded-full hover:bg-red-600 transition-all shadow-lg opacity-0 group-hover:opacity-100 cursor-pointer flex items-center justify-center"
                    >
                      <i className="ri-close-line text-lg"></i>
                    </button>
                  </div>
                ))}
                
                {formData.images.length < 8 && (
                  <div className="aspect-square">
                    <ImageUploader
                      onImageUploaded={handleImageUploaded}
                      folder="products"
                      shape="square"
                      size="medium"
                      className="w-full h-full"
                    />
                  </div>
                )}
              </div>

              <p className="text-xs text-gray-500 flex items-center">
                <i className="ri-information-line mr-1"></i>
                You can upload up to 8 images. First image will be the main product image.
              </p>
            </div>

            <div className="flex flex-col sm:flex-row gap-3 pt-6 border-t border-gray-200">
              <button
                type="button"
                onClick={() => navigate('/seller/dashboard')}
                className="flex-1 px-6 py-3 border-2 border-gray-300 text-gray-700 rounded-xl hover:bg-gray-50 transition-all font-medium whitespace-nowrap cursor-pointer"
              >
                <i className="ri-close-line mr-2"></i>
                Cancel
              </button>
              <button
                type="submit"
                disabled={saving}
                className="flex-1 px-6 py-3 bg-teal-600 text-white rounded-xl hover:bg-teal-700 transition-all disabled:opacity-50 disabled:cursor-not-allowed font-medium whitespace-nowrap cursor-pointer shadow-md hover:shadow-lg"
              >
                {saving ? (
                  <>
                    <i className="ri-loader-4-line animate-spin mr-2"></i>
                    Saving Changes...
                  </>
                ) : (
                  <>
                    <i className="ri-save-line mr-2"></i>
                    Save Changes
                  </>
                )}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}
